.footer {
  background-image: url(../images/footer/bg-footer.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 200px;

  position: fixed;
  z-index: -1;

  @include on-tablet {
    min-height: 480px;

  }

  @include on-pc {
    grid-column: 555px;
  }
}
